@media (max-width: 550px) {
    body::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-image: url(https://res.cloudinary.com/drkmgpcad/image/upload/v1726821831/ve1qltzcmjvwqbqcqvcj.png);
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        opacity: 0.5;
        z-index: -1; 
    }

    header{
 
        font-size: 1rem;
    }
    nav {

        padding: auto;
 
        border-bottom: 1px solid #EADCA6;
    }

    nav button {
        padding: 3px 7px; 
        font-size: 0.6rem; 
    
    }


    #category-header {
        text-align: center;
        padding: 5px;
        font-size: 0.7em;
        font-weight: bold;
        color: black;
    }

    #articles-container {
        column-count: 1;
        padding: 20px;
    }

    .article {

        padding: 15px;
    }


    .article img {
        width: 100%;
        height: auto;
        object-fit: cover;

    }

    .article h2 {
        font-size: 1em;
        margin: 15px;
  
    }
    
    .article p {

        font-size: .7em;
        color: black;

    }
 
    .article a {
        color: #007abf;
        font-size: .7rem;
        text-decoration: none;
        font-weight: bold;
        text-decoration: underline;
    }
    
    footer p {
        font-size: 40%; 
    }

    #back-to-top {

        bottom: 1%;
        height: 6%;
    
    }
    

    .loading-spinner {
        width: 25px;
        height: 25px;

    }
 

}